export function changeLoginPswd(data = {}, config) {
  data = {
    ...data,
    oldPwd: md5(data.oldPwd),
    newPwdOne: md5(data.newPwdOne),
    modifyType: 1
  }
  return put(`${BASE}api/modifyPwd`, qs.stringify(data), config)
}

